<template>
  <div class="panel panel-trans-flow">
    <div class="panel-title">
      <el-form label-position="top" :inline="true">
        <el-form-item label="流水号查询">
          <el-input v-model="paySequenceNo" placeholder="请输入要查询的流水号" clearable></el-input>
        </el-form-item>
        <el-form-item label="流水时间">
          <el-date-picker v-model="dates" type="daterange" align="right" unlink-panels range-separator="至" format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="支付类型">
          <el-select v-model="paymentType" placeholder="请选择" clearable>
            <el-option v-for="item in paymentTypes" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="交易状态">
          <el-select v-model="status" placeholder="请选择" clearable>
            <el-option v-for="item in transStatusList" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="hidden-label" label="查询按钮">
          <el-button type="warning" @click.stop="queryList">查询</el-button>
        </el-form-item>
      </el-form>
      <div class="result">
        <h6>搜索结果</h6>
        <ul class="flex-wrap">
          <li>
            <span>连续续费：</span><b>{{renewCount}}</b><span>笔 - </span><b>{{(renewAmount / 100).toFixed(2)}}</b><span>元</span>
          </li>
          <li>
            <span>分期付款：</span><b>{{periodCount}}</b><span>笔 - </span><b>{{(periodAmount / 100).toFixed(2)}}</b><span>元</span>
          </li>
          <li class="downPayment">
            <span>首付款：</span><b>{{paymentCount}}</b><span>笔 - </span><b>{{(paymentAmount / 100).toFixed(2)}}</b><span>元</span>
          </li>
          <li>
            <span>合计金额：</span><em>{{((paymentAmount + renewAmount + periodAmount) / 100).toFixed(2)}}</em><span>元</span>
          </li>
          <li>
            <a href="" class="mid-flex" @click.prevent.stop>提现</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="panel-body">
      <p class="flex-wrap"><span class="mid-flex btn-refresh" @click="queryList">刷新</span><span class="mid-flex btn-export" @click.prevent.stop>导出</span></p>
      <el-table :data="billList" cell-class-name="cell-container" header-cell-class-name="cell-container" header-row-class-name="table-header">
        <el-table-column prop="billNo" label="付款编号" align="center">
        </el-table-column>
        <el-table-column prop="billType" width="90" label="支付类型" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.billType | billType}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="payType" width="90" label="付款方式" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.payType | payType}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="orderNo" label="订单编号" width="130" align="center">
        </el-table-column>
        <el-table-column prop="productName" label="课程名称" align="center" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="payAmt" label="交易金额（元）" width="120" align="center">
          <template slot-scope="scope">
            <span>{{(scope.row.payAmt / 100).toFixed(2)}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="payTime" label="日期" width="160" align="center">
        </el-table-column>
        <el-table-column prop="billStatus" label="交易状态" width="120" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.billStatus | renewStatus}}</span>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page" :page-sizes="[1, 10,20,50,100]" :page-size="size" layout="total, sizes, prev, pager, next" :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getPaymentTypes, getTransStatusList } from '@/assets/js/fields.js';
import { getTransFlowList, getTransStatistics } from '@/api/index.js';
import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';

dayjs.extend(customParseFormat);

export default {
  name: 'trans-flow',
  data() {
    return {
      paySequenceNo: '',
      dates: [
        dayjs()
          .add(-1, 'month')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD'),
      ],
      status: '',
      transStatusList: getTransStatusList(),
      paymentTypes: getPaymentTypes(),
      billList: [],
      paymentType: '',
      page: 1,
      size: 10,
      total: 1,
      paymentCount: 0,
      renewCount: 0,
      periodCount: 0,
      paymentAmount: 0,
      renewAmount: 0,
      periodAmount: 0,
    };
  },
  created() {
    this.queryList();
  },

  methods: {
    queryList() {
      this.dates = this.dates || [];
      getTransFlowList(
        this.page,
        this.size,
        this.paySequenceNo,
        this.status,
        this.paymentType,
        this.dates[0],
        this.dates[1],
      )
        .then((result) => {
          this.total = (result && result.total) || 0;
          if (result && Array.isArray(result.list)) {
            this.billList = result.list;
            // this.billList = result.list.map((item) => {
            //   item.payTime = dayjs(item.payTime, 'YYYY-MM-DD hh-MM-ss').format('YYYY-MM-DD HH:mm:ss');
            //   return item;
            // });
          } else {
            this.billList = [];
          }
          console.log(result);
        })
        .catch(err => console.error(err));
      getTransStatistics(
        this.page,
        this.size,
        this.paySequenceNo,
        this.status,
        this.paymentType,
        this.dates[0],
        this.dates[1],
      )
        .then((result = {}) => {
          this.paymentCount = result.paymentCount || 0;
          this.renewCount = result.renewCount || 0;
          this.periodCount = result.periodCount || 0;
          this.paymentAmount = result.paymentAmount || 0;
          this.renewAmount = result.renewAmount || 0;
          this.periodAmount = result.periodAmount || 0;
        })
        .catch(console.error);
    },
    handleSizeChange(val) {
      this.size = val;
      this.page = 1;
      this.queryList();
    },
    handleCurrentChange(val) {
      this.page = val;
      this.queryList();
    },
  },
};
</script>

